<template>
  <div>
    <button @click="$router.back()" class="text-sm text-gray-500 dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-200 mb-4"> </button>
    <h2 class="text-xl font-medium text-gray-800 dark:text-gray-100 mb-6">Job {{ jobInstanceId }}</h2>

    <div class="bg-white dark:bg-gray-800 rounded-xl shadow-sm p-4">
      <h3 class="text-lg font-medium text-gray-700 dark:text-gray-100 mb-3">Executions</h3>
      <table class="w-full text-sm text-left border-collapse">
        <thead class="text-gray-500 dark:text-gray-400 border-b border-gray-100 dark:border-gray-700">
          <tr>
            <th class="py-2">ID</th>
            <th class="py-2">Status</th>
            <th class="py-2">Start</th>
            <th class="py-2">End</th>
            <th class="py-2">Actions</th>
          </tr>
        </thead>
        <tbody class="divide-y divide-gray-100 dark:divide-gray-700">
          <tr v-for="exe in executions" :key="exe.JOB_EXECUTION_ID" class="hover:bg-gray-50 dark:hover:bg-gray-900">
            <td class="py-2">{{ exe.JOB_EXECUTION_ID }}</td>
            <td class="py-2">
              <span :class="statusClass(exe.STATUS)">{{ exe.STATUS }}</span>
            </td>
            <td class="py-2">{{ exe.START_TIME }}</td>
            <td class="py-2">{{ exe.END_TIME }}</td>
            <td class="py-2 space-x-2">
              <button @click="viewSteps(exe.JOB_EXECUTION_ID)" class="px-2 py-1 text-sm rounded border border-gray-300 dark:border-gray-700 hover:bg-gray-100 dark:hover:bg-gray-800">Steps</button>
              <button @click="viewLogs(exe.JOB_EXECUTION_ID)" class="px-2 py-1 text-sm rounded border border-gray-300 dark:border-gray-700 hover:bg-gray-100 dark:hover:bg-gray-800">Logs</button>
              <button v-if="canStop(exe)" @click="stop(exe.JOB_EXECUTION_ID)" class="px-2 py-1 text-sm rounded bg-red-50 dark:bg-red-900 text-red-600 dark:text-red-300 hover:bg-red-100 dark:hover:bg-red-800">Stop</button>
              <button v-if="canRestart(exe)" @click="restart(exe.JOB_EXECUTION_ID)" class="px-2 py-1 text-sm rounded bg-blue-500 dark:bg-blue-700 text-white hover:bg-blue-600 dark:hover:bg-blue-800">Restart</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <div v-if="selectedSteps" class="mt-6 bg-white dark:bg-gray-800 rounded-xl shadow-sm p-4">
      <h3 class="text-lg font-medium text-gray-700 dark:text-gray-100 mb-3">Steps for Execution {{ selectedExecutionId }}</h3>
      <table class="w-full text-sm border-collapse">
        <thead class="text-gray-500 dark:text-gray-400 border-b border-gray-100 dark:border-gray-700">
          <tr>
            <th class="py-2">Step Name</th>
            <th class="py-2">Status</th>
            <th class="py-2">Read/Write/Skip</th>
          </tr>
        </thead>
        <tbody class="divide-y divide-gray-100 dark:divide-gray-700">
          <tr v-for="s in selectedSteps" :key="s.STEP_EXECUTION_ID" class="hover:bg-gray-50 dark:hover:bg-gray-900">
            <td class="py-2">{{ s.STEP_NAME }}</td>
            <td class="py-2">{{ s.STATUS }}</td>
            <td class="py-2">{{ s.READ_COUNT }} / {{ s.WRITE_COUNT }} / {{ s.SKIP_COUNT }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div v-if="selectedLogs" class="mt-6 bg-white dark:bg-gray-800 rounded-xl shadow-sm p-4">
      <h3 class="text-lg font-medium text-gray-700 dark:text-gray-100 mb-3">Logs</h3>
      <pre class="bg-gray-900 text-green-200 p-3 rounded-lg max-h-72 overflow-auto text-sm">{{ selectedLogs }}</pre>
    </div>
  </div>
</template>

<script setup>
import { onMounted, ref, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useBatchStore } from '../stores/batchStore'
import api from '../services/api'

const route = useRoute()
const jobInstanceId = route.params.jobInstanceId
const store = useBatchStore()

const executions = computed(() => store.executions[jobInstanceId] || [])
const selectedSteps = ref(null)
const selectedExecutionId = ref(null)
const selectedLogs = ref(null)

onMounted(async () => {
  await store.fetchExecutions(jobInstanceId)
})

function viewSteps(jobExecutionId) {
  selectedExecutionId.value = jobExecutionId
  store.fetchSteps(jobExecutionId).then(() => {
    selectedSteps.value = store.steps[jobExecutionId]
  })
}

function viewLogs(jobExecutionId) {
  selectedExecutionId.value = jobExecutionId
  store.fetchLogs(jobExecutionId).then(() => {
    selectedLogs.value = store.logs[jobExecutionId]
  })
}

async function stop(jobExecutionId) {
  try { await api.stopExecution(jobExecutionId); alert('Stop requested') }
  catch(e) { alert('Stop failed') }
}

async function restart(jobExecutionId) {
  try { await api.restartExecution(jobExecutionId); alert('Restart requested') }
  catch(e) { alert('Restart failed') }
}

function canStop(exe) { return exe.STATUS === 'STARTED' || exe.STATUS === 'STARTING' }
function canRestart(exe) { return exe.STATUS === 'FAILED' || exe.STATUS === 'STOPPED' }

function statusClass(status) {
  switch(status) {
    case 'COMPLETED': return 'text-green-600 font-medium'
    case 'FAILED': return 'text-red-600 font-medium'
    case 'STARTED': return 'text-blue-600 font-medium'
    default: return 'text-gray-600'
  }
}
</script>