import { defineStore } from 'pinia'
import api from '../services/api'

export const useBatchStore = defineStore('batch', {
  state: () => ({
    jobs: [],
    executions: {}, // jobInstanceId -> executions array
    steps: {}, // jobExecutionId -> steps array
    logs: {},
    loading: false,
    error: null
  }),
  actions: {
    async fetchJobs() {
      this.loading = true
      try {
        const res = await api.listJobs()
        this.jobs = res.data
      } catch (e) { this.error = e }
      finally { this.loading = false }
    },
    async fetchExecutions(jobInstanceId) {
      this.loading = true
      try {
        const res = await api.listExecutions(jobInstanceId)
        this.executions[jobInstanceId] = res.data
      } catch (e) { this.error = e }
      finally { this.loading = false }
    },
    async fetchSteps(jobExecutionId) {
      this.loading = true
      try {
        const res = await api.listStepExecutions(jobExecutionId)
        this.steps[jobExecutionId] = res.data
      } catch (e) { this.error = e }
      finally { this.loading = false }
    },
    async fetchLogs(jobExecutionId) {
      this.loading = true
      try {
        const res = await api.fetchLogs(jobExecutionId)
        this.logs[jobExecutionId] = res.data
      } catch (e) { this.error = e }
      finally { this.loading = false }
    }
  }
})