<template>
  <div class="p-6 space-y-6">
    <h2 class="text-xl font-bold text-brand"> Job </h2>

    <!-- Job  -->
    <div class="p-4 bg-white dark:bg-gray-800 rounded-xl shadow-card">
      <h3 class="font-semibold mb-2">Job </h3>
      <ul>
        <li v-for="job in jobs" :key="job" class="flex justify-between py-2">
          <span>{{ job.name }}</span>
          <span>{{ job.description }}</span>
          <button @click="showJobParams(job)"
                  class="px-3 py-1 rounded-lg bg-brand text-white hover:scale-105 transition">
            
          </button>
        </li>
      </ul>
    </div>

    <!-- Job    -->
    <div v-if="selectedJob" class="fixed inset-0 bg-black/50 flex items-center justify-center">
      <div class="bg-white dark:bg-gray-800 rounded-xl p-6 w-96">
        <h3 class="text-lg font-semibold mb-4">Job : {{ selectedJob }}</h3>
        <div v-for="key in requiredParams" :key="key" class="mb-2">
          <label class="block text-sm">{{ key }}</label>
          <input v-model="jobParams[key]" type="text"
                 class="w-full p-2 rounded border dark:border-gray-700 dark:bg-gray-700" />
        </div>
        <div class="mt-4 flex justify-end space-x-2">
          <button @click="launchSelectedJob"
                  class="px-3 py-1 bg-brand text-white rounded hover:scale-105 transition"></button>
          <button @click="closeModal"
                  class="px-3 py-1 border rounded hover:bg-gray-100 dark:hover:bg-gray-700"></button>
        </div>
      </div>
    </div>

    <!--   -->
    <div class="p-4 bg-white dark:bg-gray-800 rounded-xl shadow-card">
      <h3 class="font-semibold mb-2">  </h3>
      <table class="w-full text-sm border-separate" style="border-spacing:0;">
        <thead>
          <tr
            class="text-center border-b-2 border-brand-light dark:border-brand-dark"
            style="background: #eaf4ff; border-top-left-radius: 0.75rem; border-top-right-radius: 0.75rem; overflow: hidden;"
          >
            <th class="p-2 font-medium text-brand border-r border-brand-light dark:border-brand-dark w-20 first:rounded-tl-xl last:rounded-tr-xl">
              ID
            </th>
            <th class="p-2 font-medium text-brand border-r border-brand-light dark:border-brand-dark w-24">
              Status
            </th>
            <th class="p-2 font-medium text-brand border-r border-brand-light dark:border-brand-dark w-36">
              Start
            </th>
            <th class="p-2 font-medium text-brand border-r border-brand-light dark:border-brand-dark w-36">
              End
            </th>
            <th class="p-2 font-medium text-brand border-r border-brand-light dark:border-brand-dark w-24">
              Execution
            </th>
            <th class="p-2 font-medium text-brand w-56 last:rounded-tr-xl">
              Actions
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="exec in executions" :key="exec.job_execution_id"
              class="border-b border-gray-100 dark:border-gray-700 text-center hover:bg-gray-50 dark:hover:bg-gray-900 transition">
            <td class="p-2 border-r border-gray-100 dark:border-gray-700">{{ exec.job_execution_id || '-' }}</td>
            <td class="p-2 border-r border-gray-100 dark:border-gray-700">
              <span class="group relative inline-block align-middle">
                <!--  SVG  -->
                <svg v-if="exec.status === 'COMPLETED'" class="w-5 h-5 text-green-500" fill="none" viewBox="0 0 24 24">
                  <circle cx="12" cy="12" r="10" fill="#22c55e"/>
                  <path d="M8 12l2.5 2.5L16 9" stroke="#fff" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                </svg>
                <svg v-else-if="exec.status === 'FAILED'" class="w-5 h-5 text-red-500" fill="none" viewBox="0 0 24 24">
                  <circle cx="12" cy="12" r="10" fill="#ef4444"/>
                  <path d="M9 9l6 6M15 9l-6 6" stroke="#fff" stroke-width="2" stroke-linecap="round"/>
                </svg>
                <svg v-else-if="exec.status === 'STARTED' || exec.status === 'STARTING'" class="w-5 h-5 text-blue-500 animate-spin" fill="none" viewBox="0 0 24 24">
                  <circle cx="12" cy="12" r="10" stroke="#3b82f6" stroke-width="4" fill="none" opacity="0.3"/>
                  <path d="M12 2a10 10 0 0 1 10 10" stroke="#3b82f6" stroke-width="4" stroke-linecap="round"/>
                </svg>
                <svg v-else class="w-5 h-5 text-gray-400" fill="none" viewBox="0 0 24 24">
                  <circle cx="12" cy="12" r="10" fill="#a3a3a3"/>
                  <text x="12" y="16" text-anchor="middle" fill="#fff" font-size="10">?</text>
                </svg>
                <!--  -->
                <span
                  class="absolute left-1/2 -translate-x-1/2 mt-2 px-2 py-1 rounded bg-gray-800 text-white text-xs opacity-0 group-hover:opacity-100 pointer-events-none transition-opacity z-20 whitespace-nowrap"
                >
                  {{ exec.status || 'N/A' }}
                </span>
              </span>
            </td>
            <td class="p-2 border-r border-gray-100 dark:border-gray-700">{{ formatDateTime(exec.start_time) }}</td>
            <td class="p-2 border-r border-gray-100 dark:border-gray-700">{{ formatDateTime(exec.end_time) }}</td>
            <td class="p-2 border-r border-gray-100 dark:border-gray-700">{{ exec.execution_time || '-' }}</td>
            <td class="p-2 flex flex-wrap gap-1 justify-center">
              <button
                @click="viewSteps(exec.job_execution_id)"
                class="px-3 py-1 text-xs rounded-lg border border-blue-400 bg-blue-50 dark:bg-blue-900 text-blue-700 dark:text-blue-200 hover:bg-blue-100 dark:hover:bg-blue-800 transition font-medium shadow-sm"
              >
                Steps
              </button>
              <button
                @click="fetchLogs(exec.job_execution_id)"
                class="px-3 py-1 text-xs rounded-lg border border-amber-400 bg-amber-50 dark:bg-amber-900 text-amber-700 dark:text-amber-200 hover:bg-amber-100 dark:hover:bg-amber-800 transition font-medium shadow-sm"
              >
                Logs
              </button>
              <button
                @click="stop(exec.job_execution_id)"
                class="px-3 py-1 text-xs rounded-lg border border-red-400 bg-red-50 dark:bg-red-900 text-red-700 dark:text-red-200 hover:bg-red-100 dark:hover:bg-red-800 transition font-medium shadow-sm"
              >
                Stop
              </button>
              <button
                @click="restart(exec.job_execution_id)"
                class="px-3 py-1 text-xs rounded-lg border border-green-400 bg-green-50 dark:bg-green-900 text-green-700 dark:text-green-200 hover:bg-green-100 dark:hover:bg-green-800 transition font-medium shadow-sm"
              >
                Restart
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Step  -->
    <div v-if="selectedSteps" class="p-6 bg-white dark:bg-gray-800 rounded-2xl shadow-lg space-y-6">

  <!-- Step  -->
  <h3 class="text-xl font-semibold text-gray-800 dark:text-gray-100">Step  - Execution {{ selectedExecutionId }}</h3>

  <!--    -->
  <div class="overflow-x-auto rounded-xl shadow-sm border border-gray-200 dark:border-gray-700">
    <table class="w-full text-sm border-separate" style="border-spacing:0;">
      <thead class="bg-green-50 dark:bg-gray-700">
        <tr class="text-center text-gray-700 dark:text-gray-200 font-medium">
          <th class="p-2 w-28 first:rounded-tl-xl last:rounded-tr-xl">Step</th>
          <th class="p-2 w-28"></th>
          <th class="p-2 w-32">Rollback</th>
          <th class="p-2 w-40"></th>
          <th class="p-2 w-40"> </th>
        </tr>
      </thead>
      <tbody class="text-center">
        <tr v-for="step in selectedSteps" :key="step.step_execution_id"
            class="border-b border-gray-100 dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-900 transition">
          <td class="p-2 font-medium">{{ step.step_name }}</td>
          <td class="p-2 flex justify-center items-center space-x-2">
            <span class="relative group">
              <!--   -->
              <svg v-if="step.status === 'COMPLETED'" class="w-5 h-5 text-green-500" fill="none" viewBox="0 0 24 24">
                <circle cx="12" cy="12" r="10" fill="#22c55e"/>
                <path d="M8 12l2.5 2.5L16 9" stroke="#fff" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
              </svg>
              <svg v-else-if="step.status === 'FAILED'" class="w-5 h-5 text-red-500" fill="none" viewBox="0 0 24 24">
                <circle cx="12" cy="12" r="10" fill="#ef4444"/>
                <path d="M9 9l6 6M15 9l-6 6" stroke="#fff" stroke-width="2" stroke-linecap="round"/>
              </svg>
              <svg v-else-if="step.status === 'STARTED' || step.status === 'STARTING'" class="w-5 h-5 text-blue-500 animate-spin" fill="none" viewBox="0 0 24 24">
                <circle cx="12" cy="12" r="10" stroke="#3b82f6" stroke-width="4" fill="none" opacity="0.3"/>
                <path d="M12 2a10 10 0 0 1 10 10" stroke="#3b82f6" stroke-width="4" stroke-linecap="round"/>
              </svg>
              <svg v-else class="w-5 h-5 text-gray-400" fill="none" viewBox="0 0 24 24">
                <circle cx="12" cy="12" r="10" fill="#a3a3a3"/>
                <text x="12" y="16" text-anchor="middle" fill="#fff" font-size="10">?</text>
              </svg>
              <!--  -->
              <span class="absolute left-1/2 -translate-x-1/2 mt-2 px-3 py-1 rounded bg-gray-900 text-white text-xs opacity-0 group-hover:opacity-100 pointer-events-none transition-opacity z-20 whitespace-nowrap">
                {{ step.status }}
              </span>
            </span>
          </td>
          <td class="p-2">{{ step.rollback_count }}</td>
          <td class="p-2">{{ formatDateTime(step.last_updated) || '-' }}</td>
          <td class="p-2 text-left truncate max-w-xs">[{{ step.exit_code || 'N/A' }}] - {{ step.exit_message || '' }}</td>
        </tr>
      </tbody>
    </table>
  </div>

  <!--     -->
  <h4 class="text-lg font-semibold text-gray-800 dark:text-gray-100"></h4>
  <div class="overflow-x-auto rounded-xl shadow-sm border border-gray-200 dark:border-gray-700">
    <table class="w-full text-sm">
      <thead class="bg-green-50 dark:bg-gray-700">
        <tr class="text-center text-gray-700 dark:text-gray-200 font-medium">
          <th class="p-2 w-28 first:rounded-tl-xl last:rounded-tr-xl">Read/Wrtie</th>
          <th class="p-2 w-28">Commit</th>
          <th class="p-2 w-28">Filter</th>
          <th class="p-2 w-36">Skip(Read/Write/Process)</th>
        </tr>
      </thead>
      <tbody class="text-center">
        <tr v-for="step in selectedSteps" :key="step.step_execution_id"
            class="border-b border-gray-100 dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-900 transition">
          <td class="p-2">{{ step.read_count }}/{{ step.write_count }}</td>
          <td class="p-2">{{ step.commit_count }}</td>
          <td class="p-2">{{ step.filter_count }}</td>
          <td class="p-2">{{ step.read_skip_count }}/{{ step.write_skip_count }}/{{ step.process_skip_count }}</td>
        </tr>
      </tbody>
    </table>
  </div>

</div>


    <!-- Logs -->
    <div v-if="logs" class="mt-6 p-4 bg-gray-50 dark:bg-gray-900 rounded-xl shadow-card">
      <h3 class="font-medium mb-2"> - Execution {{ selectedLogExecutionId }}</h3>
      <pre class="text-xs whitespace-pre-wrap">{{ logs.exit_message }}</pre>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import api from '../services/api'

//    
function formatDateTime(dt) {
  if (!dt) return '-'
  const d = new Date(dt)
  return `${d.getFullYear()}-${String(d.getMonth()+1).padStart(2,'0')}-${String(d.getDate()).padStart(2,'0')} ${String(d.getHours()).padStart(2,'0')}:${String(d.getMinutes()).padStart(2,'0')}:${String(d.getSeconds()).padStart(2,'0')}`
}

const jobs = ref([])
const executions = ref([])
const selectedSteps = ref(null)
const selectedExecutionId = ref(null)
const logs = ref(null)
const selectedLogExecutionId = ref(null)
const selectedJob = ref(null)       //    ReferenceError 
const requiredParams = ref([])
const jobParams = ref({})
const loadJobs = async () => {
  jobs.value = (await api.listJobs()).data
}

const loadExecutions = async () => {
  const allExecs = []
  for (let jobInstanceId of [1,2,3]) { //  jobInstanceId 
    const res = await api.listExecutions(jobInstanceId)
    console.log(res.data)
    allExecs.push(...res.data)
  }
  executions.value = allExecs
}


const viewSteps = async (executionId) => {
  selectedExecutionId.value = executionId
  selectedSteps.value = (await api.listStepExecutions(executionId)).data
  console.log('executionId [',executionId,']->',selectedSteps.value)
}

const fetchLogs = async (executionId) => {
  selectedLogExecutionId.value = executionId
  logs.value = (await api.fetchLogs(executionId)).data
}

const stop = async (executionId) => {
  await api.stopExecution(executionId)
  await loadExecutions()
}

const restart = async (executionId) => {
  await api.restartExecution(executionId)
  await loadExecutions()
}

onMounted(async () => {
  await loadJobs()
  await loadExecutions()
})

//  Job   
const showJobParams = async (jobName) => {
  selectedJob.value = jobName
  const res = await api.getRequiredParams(jobName)
  requiredParams.value = res.data
  console.log('Required params for', jobName, ':', requiredParams.value)
  jobParams.value = {}
  res.data.forEach(key => jobParams.value[key] = '')
}

// Job 
const launchSelectedJob = async () => {
  await api.launchJob(selectedJob.value, jobParams.value)
  closeModal()
}

//  
const closeModal = () => {
  selectedJob.value = null
  requiredParams.value = []
  jobParams.value = {}
}
</script>
