import axios from 'axios'

const api = axios.create({
  baseURL: '/api',
  timeout: 10000,
})

let IP;
let PORT = import.meta.env.VITE_APP_PORT;
const mode = import.meta.env.MODE; // 'development', 'production', 'local'

console.log('Apllication-mode:', mode);
console.log('Apllication-name:', import.meta.env.VITE_APP_NAME);

switch (mode) {
case 'production':
    IP = import.meta.env.VITE_END_POINT_IP;
    break;
case 'development':
    IP = import.meta.env.VITE_END_POINT_IP;
    break;
case 'local':
    IP = import.meta.env.VITE_END_POINT_IP || 'localhost';
    break;
default:
    IP = 'localhost';
}


const fullUrl = `http://${IP}:${PORT}/api`;
export default {
  listJobs() { return api.get(fullUrl+'/batch/jobs') },
  listExecutions(jobInstanceId) { return api.get(fullUrl+`/batch/jobs/${jobInstanceId}/executions`) },
  listStepExecutions(jobExecutionId) { return api.get(fullUrl+`/batch/executions/${jobExecutionId}/steps`) },
  fetchLogs(jobExecutionId) { return api.get(fullUrl+`/batch/executions/${jobExecutionId}/logs`) },
  stopExecution(jobExecutionId) { return api.post(fullUrl+`/batch/executions/${jobExecutionId}/stop`) },
  restartExecution(jobExecutionId) { return api.post(fullUrl+`/batch/executions/${jobExecutionId}/restart`) },
  getRequiredParams(jobName) { return api.get(fullUrl+`/batch/jobs/${encodeURIComponent(jobName)}/required-params`) },
  launchJob(jobName, params) { return api.post(fullUrl+`/batch/jobs/${encodeURIComponent(jobName)}/launch`, params || {}) },
  
}